<template>
  <page
    ref="page"
    :headerHeightVW="headerHeightVW"
    class="index-page"
    @on-show="onShow"
    @on-ready="onReady"
  >
    <template slot="blank">
      <img class="launch" src="../../assets/images/launch.png">
    </template>
  </page>
</template>

<script>
import Page from '../../components/Page'
import navigate from '../../utils/navigate.js'
import io from '../../api'
import db from '../../db'
import native from '../../utils/native.js'
import config from '../../config'

export default {
  components: {
    Page
  },
  data: () => ({
    headerHeightVW: 0
  }),
  methods: {
    onReady () {
      // alert(api.deviceId)

      if (typeof api != 'undefined') {
        let deviceId = (api.deviceId + '').replace(/-/g, '')
        console.log(deviceId)
        localStorage.setItem('deviceToken', deviceId)

        const ajpush = api.require('ajpush')
        ajpush.setListener((ret, err) => {
          console.log(JSON.stringify(ret))
          
          this.onMessage(ret.extra)
        })

        api.addEventListener({
          name: 'appintent'
        }, (ret, err) => {

          ajpush.setBadge({
            badge:0
          })
          this.onMessage(ret.value ? ret.value.extra : ret.appParam.ajpush.extra)
        })

        api.addEventListener({
          name: 'noticeclicked'
        }, (ret, err) => {

          ajpush.setBadge({
            badge:0
          })
          console.log('@@@@@@@@@@@@@')
          this.onMessage(ret.value ? ret.value.extra : ret.appParam.ajpush.extra)
        })
      }
      

      // 选择网络
      // this.$dialog.confirm({
      //   title: '内部网络链接',
      //   message: '您将链接到内部网路，是否继续？'
      // }).then(() => {
      //   localStorage.setItem('network', 'in')
      //   this.onLoad()
      // }).catch(() => {
      //   localStorage.setItem('network', 'out')
      //   this.onLoad()
      // })

      this.onLoad()
    },
    onMessage (extra) {
      console.log('@@@@@@@@@@@@@')
      console.log(JSON.stringify(extra))
      if (extra.version !== config.version) {
        return false
      }

      if (extra.forceOut && extra.deviceId != localStorage.getItem('deviceToken') && window.localStorage.getItem('token')) {

        window.localStorage.removeItem('token')
        
        navigate.redirectLogin()

        api.alert({
          msg: '您的帐号在其他设备登录，您已被强制退出！',
          buttons: ['确认']
        })
      } else {

        api.confirm({
          title: extra.title,
          msg: extra.content,
          buttons: ['去处理', '取消']
        }, (ret) => {
          let index = ret.buttonIndex

          if (index == 1) {
              if (extra.type == 'order') {
                navigate.open('orderdetail', {id: extra.id})
              }

              if (extra.type == 'problem') {
                navigate.open('problemdetail', {id: extra.id})
              }

              if (extra.type == 'transmit') {
                navigate.open('transmitdetail', {id: extra.id})
              }
          }
        })

        // this.$dialog.confirm({
        //   title: extra.title,
        //   content: extra.content,
        //   // confirmButtonText: '去处理'
        // }).then(() => {
        //   if (extra.type == 'order') {
        //     navigate.open('orderdetail', {id: extra.id})
        //   }

        //   if (extra.type == 'problem') {
        //     navigate.open('problemdetail', {id: extra.id})
        //   }

        //   if (extra.type == 'transmit') {
        //     navigate.open('transmitdetail', {id: extra.id})
        //   }
        // }).catch(() => {})
      }
    },
    onLoad () {
      const token = window.localStorage.getItem('token')

      if (!token) {
        navigate.redirectLogin()
      } else {

        this.onApp()
      }
    },
    onShow () {
      

      setTimeout(() => {
        // 选择网络
        // this.$dialog.confirm({
        //   title: '内部网络链接',
        //   message: '您将链接到内部网路，是否继续？'
        // }).then(() => {
        //   localStorage.setItem('network', 'in')
        //   this.onLoad()
        // }).catch(() => {
        //   localStorage.setItem('network', 'out')
        //   this.onLoad()
        // })

        this.onLoad()
      }, 50)
    },

    onApp () {
      if (native.connection()) {
        // 加载故障配置
        io.getOrderConfig().then(data => {
          this.addressOptions = data.address

          debugger
          
          localStorage.setItem('level_options', JSON.stringify(data.level))
          localStorage.setItem('reason_options', JSON.stringify(data.reason))
          localStorage.setItem('type_options', JSON.stringify([]))
          // localStorage.setItem('line_options', JSON.stringify(data.address))
          localStorage.setItem('profession_options', JSON.stringify(data.profession))
          localStorage.setItem('source_options', JSON.stringify(data.source))

          // 加载问题配置
          io.getProblemConfig().then(data => {
            localStorage.setItem('problem_level', JSON.stringify(data.level))
            localStorage.setItem('problem_category', JSON.stringify(data.category))
            localStorage.setItem('problem_flow', JSON.stringify(data.processflow))
            // localStorage.setItem('problem_address', JSON.stringify(data.address))
            localStorage.setItem('problem_profession', JSON.stringify(data.profession))
            localStorage.setItem('problem_source', JSON.stringify(data.source))
            localStorage.setItem('problem_contractor', JSON.stringify(data.contractor))

            io.getDeviceAddress().then(data => {
              db.saveAddress(data.list).then(() => {
                io.getDeviceCategory().then(data => {
                  db.saveDeviceCategory(data.list).then(() => {

                    if (window.localStorage.getItem('token')) {
                      navigate.open('home')
                    } else {
                      navigate.redirectLogin()
                    }
                  })
                }).catch(msg => {
                  this.$toast(msg)
                })
              })
            }).catch(msg => {
              this.$toast(msg)
            })

            // navigate.open('home')
          }).catch(msg => {
            this.$toast(msg)
          })
        }).catch(msg => {
          this.$toast(msg)
        })
      } else {
        navigate.open('home')
      }
    }
  }
}
</script>

<style lang="scss">
.index-page{
  .launch{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100%;
    width: 100%;
  }
}
</style>

